import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { BarcodeScanner , BarcodeScannerOptions } from '@ionic-native/barcode-scanner';
import { NgForm } from '@angular/forms/src/directives';





 // กำหนดรูปแบบของชนิดข้อมูลผ่าน interface
interface UserLogin{
  scan:string,
  name:string
}


// interface UserScan{
//   scannedData:string,
 
// }



@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public user:UserLogin; // กำหนดตัวแปร
 
  // public scanner:UserScan; // กำหนดตัวแปร
  
  options: BarcodeScannerOptions;
  encodText:string='';
  encodedData:any={};
  scannedData:any={};


 


  constructor(public navCtrl: NavController,  private scanner: BarcodeScanner) {

    // public navCtrl: NavController, public navParams: NavParams,private barcodeScanner: BarcodeScanner
    this.user = {
      scan:'',
      name:''
    }



  }


 // share(slidingItem: ItemSliding) {
 //    slidingItem.close();
 //  }


task = {
      name: '',
      code: '',
      id: 0
    };
    tasks = [];

    onClick(){
      if(this.task.id == 0){
        this.tasks.push({id: (new Date()).getTime(),name: this.task.name, code: this.task.code, strike: false});
      }
      
      this.task = {
        name: '',
        code: '',
        id: 0
      };
    }
    
    onEdit(item){
      this.task = item;
    }

    onDelete(item){
      for(var i = 0;i < this.tasks.length; i++){
        if(item.id == this.tasks[i].id){
          this.tasks.splice(i,1);
          break;
        }
      }
    }

    onStrike(item){
      for(var i = 0;i < this.tasks.length; i++){
        if(item.id == this.tasks[i].id){
          if(this.tasks[i].strike){
            this.tasks[i].strike = false;
          }
          else{
            this.tasks[i].strike = true;
          }
          break;
        }
      }
    }

scan(){
  this.options= {
    prompt: 'Scan you barcode'
  }

    this.task.code =  this.scannedData.text;

    this.scanner.scan(this.options).then((data) => {
    this.scannedData = data;

    
     console.log(data);

  }, (err) => {
    console.log('Error :', err);
  })
  }
  encode(){
  this.scanner.encode(this.scanner.Encode.TEXT_TYPE, this.encodText).then((data) => {
  this.encodedData = data;
  }, (err) => {

  console.log('Error :', err);
  })
  }



ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }
   

submitted = false;


  doLogin(){
     this.submitted = true;


     console.log(this.user.scan);
    console.log(this.scannedData);

   
  }  

onSubmit(form: NgForm) {
    console.log(form);
}



}






